
/* src/component/Popup/SettingsForm.module.scss */
.settingsForm {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-base);
  padding: var(--spacing-base);
}

.field {
  display: flex;
  align-items: center;
  gap: var(--spacing-base);
}

.switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 20px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: 0.2s;
  border-radius: 10px;
}

.slider::before {
  position: absolute;
  content: '';
  height: 16px;
  width: 16px;
  left: 2px;
  bottom: 2px;
  background-color: white;
  transition: 0.2s;
  border-radius: 50%;
}

.switch input:checked + .slider {
  background-color: var(--color-primary);
}

.switch input:checked + .slider::before {
  transform: translateX(20px);
}

.label {
  font-size: 1rem;
}

.saveButton {
  align-self: flex-end;
  padding: calc(var(--spacing-base) / 2) var(--spacing-base);
  background: var(--color-primary);
  color: #fff;
  border: none;
  border-radius: var(--radius-content);
  cursor: pointer;
}
